html {
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased; }

body {
  background-color: #fff;
  color: #000;
  font-family: "Barlow", sans-serif;
  font-size: 18px;
  font-weight: normal;
  line-height: 30px;
  margin: 0;
  margin-top: 0 !important;
  overflow-x: hidden; }

*:focus {
  outline: none !important; }

button,
input:focus,
input[type="button"],
input[type="reset"],
input[type="submit"],
textarea:focus,
.button,
.gallery img {
  transition: all 0.2s ease-in-out; }

a {
  color: #0073e5;
  text-decoration: underline;
  transition: color 0.2s ease-in-out, background-color 0.2s ease-in-out; }
  a:hover {
    color: #333;
    text-decoration: none; }

p {
  margin: 0 0 30px;
  padding: 0; }

ol,
ul {
  margin: 0;
  padding: 0; }

li {
  list-style-type: none; }

hr {
  border: 0;
  border-collapse: collapse;
  border-bottom: 1px solid #000;
  clear: both;
  color: #000;
  margin: 1.65em auto; }

b,
strong {
  font-weight: 700; }

blockquote,
cite,
em,
i {
  font-style: italic; }

mark {
  background: #ddd;
  color: #333; }

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Barlow", sans-serif;
  font-weight: bold;
  line-height: 1.2;
  margin: 0 0 20px; }

h1 {
  font-size: 30px; }

h2 {
  font-size: 50.29px;
  line-height: 57.45px; }

h3 {
  font-size: 42.81px;
  line-height: 48.92px; }

h4 {
  font-size: 37.7px;
  line-height: 43.09px; }

h5 {
  font-size: 24px; }

h6 {
  font-size: 18px; }

.tl {
  text-align: left; }

.tr {
  text-align: right; }

.tc {
  text-align: center; }

.tj {
  text-align: justify; }

/* Objects
--------------------------------------------- */
embed,
iframe,
img,
object,
video,
.wp-caption {
  max-width: 100%; }

img {
  height: auto;
  vertical-align: top; }

figure {
  margin: 0; }

